*{
  margin:0;
}

.app__header{
  display:flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

body{
  background-color: #f5f6fa;
}

.app__dropdown{
  background-color: white;
}

.app__header > h1{
  color: #fc3c3c;
  font-size: 2rem;
}

.app__graphTitle{
  margin-top:20px;
}

.app__graph{
  flex-grow: 1;
}

.app__stats{
  display:flex;
  align-items: center;
  justify-content: space-between;
}

.app__right{
  display:flex;
  flex-direction: column;
}

.app__right > .MuiCardContent-root{
  display:flex;
  flex-direction: column;
  flex-grow:1;
}

.app{
  margin-top:10px;
  display:flex;
  justify-content: space-evenly;
  padding:20px;
}

@media(max-width: 990px){
  .app{
    flex-direction: column;
  }
}

.app__left{
  flex:.8;
}